<template>
  <li
    class="d-flex align-items-center p-3"
    v-if="ambassador"
    :style="`cursor: ${cursorStyle(ambassador.mark)}`"
  >
    <BaseAvatar
      :profileImg="ambassador.user_profile.profile_picture"
      :profile_id="ambassador.user_profile.id"
      :title="ambassador.name"
      :gender="ambassador.gender"
      avatarClass="rounded-circle avatar-40"
    />
    <div class="d-flex align-items-center w-100 row">
      <div class="col-lg-3 col-md-3 col-sm-12 ms-3">
        <h6 class="d-inline-block">
          <strong>{{ ambassador.name }} </strong>
        </h6>
      </div>
      <div class="col-lg-5 col-md-5 col-sm-12 form-check mt-2">
        <div class="d-block w-100">
          <div class="progress">
            <div
              v-if="ambassador.mark.length > 0 && ambassador.mark[0].is_freezed"
              :class="`${markClass(-1)}`"
              class="progress-bar progress-bar-striped"
              role="progressbar"
              aria-valuenow="90"
              aria-valuemin="0"
              aria-valuemax="100"
              :style="`width: ${100}%;`"
            ></div>
            <div
              v-else
              :class="`${markClass(getMark(ambassador.mark))}`"
              class="progress-bar progress-bar-striped"
              role="progressbar"
              aria-valuenow="90"
              aria-valuemin="0"
              aria-valuemax="100"
              :style="`width: ${getMark(ambassador.mark)}%;`"
            ></div>
          </div>
        </div>
      </div>
      <div class="col-lg-3 col-md-3 col-sm-12 text-center">
        <router-link
          :to="{
            name: 'group.listOneAmbassadorReading',
            params: { ambassador_id: ambassador.id, week_id: week_id },
          }"
        >
          <button
            class="badge ms-0 ms-md-3 my-1 my-md-0 w-75 text-center border-0 outline-none"
            :class="{
              'bg-primary': ambassador.mark.length > 0,
              'bg-secondary': ambassador.mark.length == 0,
            }"
            :disabled="ambassador.mark.length == 0"
            :style="`cursor: ${cursorStyle(ambassador.mark)}`"
          >
            عرض
          </button>
        </router-link>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  name: "Achievement Progress",
  props: {
    ambassador: {
      type: [Object],
      required: true,
    },
    week_id: {
      type: [Number],
      required: true,
    },
  },
  data() {
    return {};
  },
  methods: {
    /**
     * return mark color class.
     *  @param  mark
     * @return class
     */
    markClass(mark) {
      switch (mark) {
        case -1:
          return "freeze";
        case 100:
          return "full-mark";

        case 0:
          return "zero-mark";

        default:
          return "incomplete";
      }
    },

    getMark(mark) {
      if (mark?.length > 0) {
        return mark[0].reading_mark + mark[0].writing_mark + mark[0].support;
      }
    },

    cursorStyle(mark) {
      if (mark?.length == 0) {
        return "not-allowed";
      }
      return "pointer";
    },
  },
};
</script>

<style scoped>
.full-mark {
  --bs-bg-opacity: 1;
  background-color: rgba(
    var(--bs-primary-rgb),
    var(--bs-bg-opacity)
  ) !important;
}

.zero-mark {
  width: 100% !important;
  --bs-bg-opacity: 1;
  background-color: rgba(var(--bs-danger-rgb), var(--bs-bg-opacity)) !important;
}

.incomplete {
  --bs-bg-opacity: 1;
  background-color: rgba(
    var(--bs-warning-rgb),
    var(--bs-bg-opacity)
  ) !important;
}
.freeze {
  --bs-bg-opacity: 1;
  background-color: #223e7f !important;
}
</style>
